import {baseUrl} from '@/utils/global'

export default {
    method: 'get',
    // url
    baseUrl: baseUrl,
    // 
    headers: {
        'Content-type': 'application/json;charset=UTF-8'
    },
    // 
    data: {},
    // 
    timeout: 60000,
    // 
    withCredentials: true,
    // 
    responseType: 'json'
}


