import axios from '../axios'

/*
 * redis
 */

// redis
export const info = (params) => {
    return axios({
        url: '/system/redis/info',
        method: 'get',
        params
    })
}

//  redis key 
export const keysSize = (params) => {
    return axios({
        url: '/system/redis/keysSize',
        method: 'get',
        params
    })
}

//  redis 
export const memoryInfo = (params) => {
    return axios({
        url: '/system/redis/memoryInfo',
        method: 'get',
        params
    })
}



