import axios from 'axios';
import config from './config';
import router from '@/router'
import qs from 'qs';

// vuexloading
// import store from '@/store'

export default function $axios(options) {
    return new Promise((resolve, reject) => {
        const instance = axios.create({
            baseURL: config.baseUrl,
            headers: config.headers,
            timeout: config.timeout,
            // withCredentialsmockjs  mockjswithCredentialsfalse
            withCredentials: config.withCredentials
        })

        // request 
        instance.interceptors.request.use(
            config => {
                let token = sessionStorage.getItem('token')
                // 1.  vuex  loading 
                // console.log(store.state.loading)
                // console.log('...')
                // 2. token
                if (token) {
                    config.headers.Authorization = token
                } else {
                    // 
                    router.push('/login')
                }
                // 3. 
                if (config.method === 'post') {
                    // if (config.url === '/login') {
                    //   config.headers = {"Content-Type": "application/json;charset=UTF-8"}
                    // } else if (config.url.concat("/delete")){
                    //   config.headers['Content-Type'] = 'application/json;charset=UTF-8'
                    // }else {
                    //   // if (config.data.__proto__ === FormData.prototype
                    //   //     || config.url.endsWith('path')
                    //   //     || config.url.endsWith('mark')
                    //   //     || config.url.endsWith('patchs')
                    //   // ) {
                    //   // } else {
                    //   //   config.data = qs.stringify(config.data,{ arrayFormat: 'indices', allowDots: true })
                    //   // }
                    // }
                }
                return config
            },

            error => {
                // 
                console.log('request:', error)
                // 1. 
                if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
                    console.log('timeout')
                    // return service.request(originalRequest);// 
                }
                // 2. 
                const errorInfo = error.response
                console.log(errorInfo)
                if (errorInfo) {
                    error = errorInfo.data  // catch,Promise.reject
                    const errorStatus = errorInfo.status; // 404 403 500 ...
                    router.push({
                        path: `/error/${errorStatus}`
                    })
                }
                return Promise.reject(error) // (catch)
            }
        )

        // response 
        instance.interceptors.response.use(
            response => {
                let data;
                // IE9response.dataundefinedresponse.request.responseText(Stringify)
                if (response.data == undefined) {
                    data = JSON.parse(response.request.responseText)
                } else {
                    data = response.data
                }

                // code
                switch (data.rc) {
                    case 1:
                        console.log(data.desc)
                        break;
                    case 0:
                        store.commit('changeState')
                    // console.log('')
                    default:
                }
                // code
                // const err = new Error(data.desc)
                // err.data = data
                // err.response = response
                // throw err

                return data
            },
            err => {
                if (err && err.response) {
                    switch (err.response.status) {
                        case 400:
                            err.message = ''
                            break
                        case 401:
                            err.message = ''
                            sessionStorage.removeItem('token')
                            router.push(`/login`)
                            break
                        case 403:
                            err.message = ''
                            break
                        case 404:
                            err.message = `: ${err.response.config.url}`
                            break
                        case 408:
                            err.message = ''
                            break
                        case 500:
                            err.message = ''
                            break
                        case 501:
                            err.message = ''
                            break
                        case 502:
                            err.message = ''
                            break
                        case 503:
                            err.message = ''
                            break
                        case 504:
                            err.message = ''
                            break
                        case 505:
                            err.message = 'HTTP'
                            break
                        default:
                    }
                }
                console.error(err)
                return Promise.reject(err) // 
            }
        )

        // 
        instance(options).then(res => {
            resolve(res)
            return false
        }).catch(error => {
            reject(error)
        })
    })
}



