import axios from '../axios'

/********app*********/
export const appConfs = (params) => {
    return axios({
        url: '/admin/appConfs',
        method: 'get',
        params
    })
}
// appcreateAppConf
export const updateAppConf = (data) => {
    return axios({
        url: '/admin/updateAppConf',
        method: 'post',
        headers: {
            'Content-Type': 'application/json'
        },
        data
    })
}
// app
export const createAppConf = (data) => {
    return axios({
        url: '/admin/createAppConf',
        method: 'post',
        headers: {
            'Content-Type': 'application/json'
        },
        data
    })
}
// app
export const apps = (params) => {
    return axios({
        url: '/admin/apps',
        method: 'get',
        params
    })
}
//app
export const createNewApp = (data) => {
    return axios({
        url: '/admin/createNewApp',
        method: 'post',
        headers: {
            'Content-Type': 'application/json'
        },
        data
    })
}
//app
export const updateApp = (data) => {
    return axios({
        url: '/admin/updateApp',
        method: 'post',
        headers: {
            'Content-Type': 'application/json'
        },
        data
    })
}

/************************/
export const getImg = (params) => {
    return axios({
        url: '/admin/getImg',
        method: 'get',
        headers: {
            "appid": "1"
        },
        params
    })
}
//
export const uploadImg = (data) => {
    return axios({
        url: '/admin/uploadImg',
        method: 'post',
        headers: {
            "Content-Type": "multipart/form-data",
            "appid": "1"
        },
        data
    })
}
//
export const deleteImg = (data) => {
    return axios({
        url: '/admin/deleteImg',
        method: 'post',
        headers: {
            "appid": "1"
        },
        data
    })
}

/************************/
//Excel
export const syncExcelToMysql = (data) => {
    return axios({
        url: '/admin/syncExcelToMysql',
        method: 'post',
        headers: {
            "Content-Type": "multipart/form-data"
        },
        data
    })
}




